.notFoundPage {
    left: 0;
    top: 0;
    min-width: 100%;
    min-height: 100%;
    background: rgba(0, 0, 0, 0.3);
    position: fixed;
    z-index: 0;
}

.notFoundContainer {
    padding: 20px;
    border-radius: 5px;
    top: 50%;
    left: 50%;
    opacity: 1;
    position: fixed;
    width: 800px;
    height: 470px;
    background-color: #edeef0;
    transform: translate(-50%, -50%);
}

.notFoundBody {
    font-size: 1.2rem;
    margin-bottom: 10px;
    text-align: center;
}

.notFoundPath {
    font-weight: bold;
}

.errorName {
    font-size: 1.3rem;
    font-style: italic;
    text-align: left;
}

.errorDescription {
    text-align: left;
}

.sadSmile {
    width: 250px;
}

@media (max-width: 1200px) {

    .notFoundContainer {
        padding: 15px;
        width: 700px;
        height: 470px;
    }

}

@media (max-width: 990px) {

    .notFoundContainer {
        padding: 15px;
        width: 650px;
        height: 435px;
    }

}
@media (max-width: 770px) {

    .notFoundContainer {
        padding: 15px;
        width: 650px;
        height: 455px;
        max-width: 100%;
    }

}

@media (max-width: 575px) {
    .sadSmile {
        width: 200px;
    }
    .notFoundContainer {
        padding: 10px;
        width: 550px;
        height: 375px;
    }

}

@media (max-width: 480px) {
    .sadSmile {
        width: 170px;
    }

}

@media (max-width: 375px) {
    .sadSmile {
        width: 150px;
    }

    .notFoundContainer {
        height: 355px;
    }
}

@media (max-width: 320px) {
    .notFoundContainer {
        height: 325px;
    }

}