.UserMessageFormWindow {
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5);
    position: fixed;
    z-index: 1000;
}

.UserMessageForm {
    border-radius: 5px;
    top: 50%;
    left: 50%;
    opacity: 1;
    position: fixed;
    width: 500px;
    height: 300px;
    background-color: #edeef0;
    transform: translate(-50%, -50%)
}

.UserMessageFormTitle {
    display: flex;
    flex: 1 1 0;
}

.UserMessageFormImg {
    border-radius: 50%;
    width: 75px;
}


.UserMessageFormHeader {
    border-radius: 4px 4px 0px 0px;
    background-color: #5682a3;
    padding: 15px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.UserMessageFormHeaderButtons {
    display: flex;
    cursor: pointer;
}

.UserMessageFormCloseBtn {
    position: absolute;
    font-size: 2rem;
    color: #d00;
    font-weight: bold;
    top: 4px;
    right: 15px;
}

.UserMessageFormRedirectBtn {
    margin-right: 30px;
    color: #fff;
}

.UserMessageFormTitle {
    color: #fff;
}

.UserMessageFormRedirectBtn:hover {
    text-decoration: underline;
}

.UserMessageFormItem {
    display: flex;
    flex-direction: row;
    margin: 10px;
}

.UserMessageFormInfo {
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    align-self: center;
}

.UserMessageFormReceiver {
    font-size: 1.3rem;
    transform: translateY(-50%);
}

.UserMessageFormTextarea {
    text-align: center;
}

.UserMessageForm form {
    text-align: center;
}

.UserMessageForm textarea {
    display: block;
    margin: 0 auto;
    width: 85%;
    height: 100px;
    font-size: 1rem;
    background-color: #fff;
    border: 1px solid #e7e8ec;
    border-radius: 5px;
    padding: 15px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
}

textarea:focus {
    outline: #0000006b;
    border: 1px solid #0000006b;
}

.UserMessageFormFooter {
    display: flex;
}

.UserMessageFormAddMessage {
    position: absolute;
    /* align-self: end; */
    right: 15px;
    bottom: 15px;
    background-color: #fff;
    border-radius: 6px;
    border: 1px solid #e7e8ec;
    display: inline-block;
    cursor: pointer;
    color: black;
    font-size: 0.75rem;
    font-weight: bold;
    padding: 6px 12px;
    text-decoration: none;
    transition: background-color .1s linear,
    color .1s linear;
}

.UserMessageFormAddMessage:hover {
    background-color: rgba(137, 160, 179, 0.5);
    color: #fff;
}

.UserMessageFormAddMessage:focus {
    outline: #0000006b;
    border: 1px solid #0000006b;
}

@media (max-width: 575px) {

    .UserMessageForm {
        width: 350px;
    }

    .UserMessageForm textarea {
        padding: 10px;
    }
}

@media (max-width: 480px) {

    .UserMessageForm {
        width: 300px;
        height: 270px;
    }

    .UserMessageForm textarea {
        height: 90px;
    }

    .UserMessageFormImg {
        width: 50px;
    }
}

@media (max-width: 375px) {

    .UserMessageForm {
        width: 280px;
        height: 245px;
    }

    .UserMessageForm textarea {
        height: 90px;
        padding: 5px;
    }

    .UserMessageFormImg {
        width: 50px;
    }

    .UserMessageFormHeader {
        padding: 10px;
    }

    .UserMessageFormCloseBtn {
        top: 0px;
    }

    .UserMessageFormItem {
        margin: 5px;
    }
}

@media (max-width: 320px) {

    .UserMessageForm {
        width: 255px;
        height: 215px;
    }

    .UserMessageForm textarea {
        height: 65px;
    }

}