.setDateBtn {
    background-color: transparent;
    border-radius: 6px;
    border: 1px solid #e7e8ec;;
    display: inline-block;
    cursor: pointer;
    color: #fff;
    font-size: 0.75rem;
    font-weight: bold;
    padding: 6px 12px;
    margin-bottom: 5px;
    text-decoration: none;
}

.setDateBtn:hover {
    background-color: #fff;
    color: black;
}

.setDateBtn:focus {
    outline: #0000006b;
    border: 1px solid #0000006b;
}

.setDateForm {
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
}

.setDateBtn:disabled {
    background-color: #fff;
    color: grey;
}

.setDateForm span {
    margin-right: 10px;
}

.setDateForm input {
    border: 1px solid #0000006b !important;
    border-radius: 5px;
}

.setDateForm input:focus {
    outline: #0000006b !important;
}