.dialogsPage {
    margin-left: 10px;
    grid-area: c;
    position: relative;
}

.chat {
    height: 100%;
}

.messages {
    padding: 0px;
    display: flex;
    flex-direction: column-reverse;
    height: 80vh;
    background: white;
    overflow: auto;
    direction: revert;
    border-radius: 5px;
    max-width: 784px;
}

.message {
    color: black;
    margin-bottom: 10px;
    font-size: 1rem;
    padding: 5px;
    opacity: 0;
    animation: show .2s 1; /* Название, прододжительность, к-ство повторений */
    animation-fill-mode: forwards; /* Чтобы элемент оставался в конечном состоянии анимации */
    animation-delay: 0.1s; /* Задержка перед началом */
    position: relative;
}

.messageDeleted {
    display: none;
}


@keyframes show {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

.messageSend {
}

.messageReceived {
    margin-left: 30px;
    width: 75%;
    display: flex;
}

.messageSend {
    margin-right: 30px;
    display: flex;
    flex-direction: row-reverse;
}

.messageBody {
    align-self: center;
    display: inline-block;
    position: relative;
    border-radius: 5px;
    transition: opacity .1s linear;
}

.messageBody span {
    padding: 3px;
}

.messageSelected {
    background-color: rgba(137, 160, 179, 0.5);
    transition: background-color .1s linear;
}

.messageSelected .messageBody {
    opacity: 0.5;
}


.received {
    background: #d9e9f5;
    border: 2px solid #d9e9f5;
    margin-left: 15px;
}

.send {
    background: #f9f9f9;
    border: 2px solid #f9f9f9;
    margin-right: 15px;
}

.received:after {
    content: '';
    position: absolute;
    width: 0;
    height: 0;
    border-bottom: 15px solid #d9e9f5;
    border-left: 15px solid transparent;
    border-right: 15px solid transparent;
    bottom: -2px;
    left: -15px;
}

.received:before {
    content: '';
    position: absolute;
    width: 0;
    height: 0;
    border-bottom: 17px solid #d9e9f5;
    border-left: 16px solid transparent;
    border-right: 16px solid transparent;
    bottom: -2px;
    left: -17px;
}

.send:after {
    content: '';
    position: absolute;
    width: 0;
    height: 0;
    border-bottom: 15px solid #f9f9f9;
    border-left: 15px solid transparent;
    border-right: 15px solid transparent;
    bottom: -2px;
    right: -15px;
}

.send:before {
    content: '';
    position: absolute;
    width: 0;
    height: 0;
    border-bottom: 17px solid #f9f9f9;
    border-left: 16px solid transparent;
    border-right: 16px solid transparent;
    bottom: -2px;
    right: -17px;
}

.check {
    display: inline-block;
    width: 0px;
    height: 3px;
    margin: 5px 6px 0px 6px;
    border: solid #bdbdbd;
    border-width: 0 2px 2px 0;
    transform: rotate(45deg);
}

.check.viewed {
    border: solid green;
    border-width: 0 2px 2px 0;
}

.deleted {
    display: none;
}

.confirmDeleting {
    margin: -15px 0;
}

.messageDate,
.messageTime {
    margin-left: 5px;
    bottom: 0;
    right: 0;
    font-size: 0.95rem;
    vertical-align: top;
}

.messageTime.send {
    color: #8f919a;
    margin-right: 0;
}

.messageTime.received {
    color: #5682a3;
    margin-left: 0;
}

.setMessageBtn {
    background-color: #5682a3;
    width: 30px;
    height: 30px;
    position: absolute;
    display: inline-block;
    transform: rotate(45deg);
    border-radius: 50%;
    opacity: .04;
    right: 10px;
    transition: opacity .1s linear;
}

.setMessageBtn.setMessageBtnReceived {
    left: 10px;
}

.setMessageBtnInner {
    transform: translateY(50%);
    position: absolute;
    width: 8px;
    height: 17px;
    border: solid #fff;
    border-width: 0 2px 2px 0;
    top: -4px;
    left: 10px;
}


.setMessageBtn:hover {
    opacity: 1;
    cursor: pointer;
}

.deleteBtn {
    position: absolute;
    left: 20px;
    transform: translateY(-50%);
}

.btnReceived {
    right: 20px;
    left: auto;
}

.deleteBtn {
    top: 50%;
    background-color: #fff;
    border-radius: 6px;
    border: 1px solid #e7e8ec;
    display: inline-block;
    cursor: pointer;
    color: black;
    font-size: 0.75rem;
    font-weight: bold;
    padding: 6px 12px;
    text-decoration: none;
    transition: background-color .1s linear,
    color .1s linear;
}

.deleteBtn:hover {
    background-color: rgba(137, 160, 179, 0.5);
    color: #fff;
}

.deleteBtn:focus {
    outline: #0000006b;
    border: 1px solid #0000006b;
}


.messageReceived:hover {
}

.messagesImg {
    width: 150px;
}

.SetlastDateForm {
}

.SetlastDateForm__items {
    display: flex;
}

.SetlastDateForm__items input {
    width: 30px;
}

.splitDate {
    color: #00000075;
    text-align: center;
    display: block;
    font-size: 0.85rem;
    font-style: italic;
}

.utils {
    margin-left: 10px;
    display: flex;
    flex-direction: row;
    align-items: baseline;
}

.messageContainer {
    padding-left: 10px;
    display: flex;
}

.text {
    text-align: start;
    overflow-wrap: break-word;
    max-width: 400px;
}

.messageForm textarea {
    margin-bottom: 25px;
    width: 100%;
    margin-top: 10px;
    height: 80px;
    font-size: 1rem;
    background-color: #fff;

    border: 1px solid #e7e8ec;
    border-radius: 5px;
    padding: 15px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
}

.messageForm span {
    position: absolute;
    bottom: 2px;

}

.messageForm textarea:focus {
    outline: #0000006b;
    border: 1px solid #0000006b;
}

.messageForm button {
    display: block;
    position: absolute;
    bottom: 0;
    right: 0;
    padding: 6px 12px;
    cursor: pointer;
    background-color: #fff;
    border-radius: 6px;
    border: 1px solid #e7e8ec;
    color: black;
    font-size: 0.75rem;
    font-weight: bold;
    text-decoration: none;
    transition: background-color .1s linear,
    color .1s linear;
}

.messageForm button:hover {
    background-color: rgba(137, 160, 179, 0.5);
    color: #fff;
}

.messageForm button:focus {
    outline: #0000006b;
    border: 1px solid #0000006b;
}

.photo {
    border-radius: 50%;
    width: 70px;
}

.SetLastDateForm {
    background: rgba(0, 0, 0, 0.5);
    width: 100%;
    display: flex;
    border-radius: 5px 5px 0 0;
    position: absolute;
    z-index: 2;
}

.SetLastDateFormInner {
    padding: 10px;
    margin: 20px auto 5px;
}

.SetLastDateForm.passive {
    height: 0;
    display: none;
}

.openMessageForm {
    display: block;
    padding: 6px 12px;
    cursor: pointer;
    background-color: transparent;
    border-radius: 6px;
    border: 1px solid #e7e8ec;
    color: black;
    font-size: 0.75rem;
    font-weight: bold;
    text-decoration: none;
    transition: background-color .1s linear,
    color .1s linear;
}

.toggleDateForm {
    background-color: transparent;
    position: absolute;
    border-radius: 6px;
    border: 0px solid #e7e8ec;
    display: inline-block;
    cursor: pointer;
    color: black;
    font-size: 0.75rem;
    font-weight: bold;
    padding: 6px 12px;
    margin-bottom: 5px;
    text-decoration: none;
    transition: background-color .1s linear,
    color .1s linear;
    z-index: 3;
}

.toggleDateForm:hover {
    background-color: rgba(137, 160, 179, 0.5);
    color: #fff;
}

.toggleDateForm:focus {
    outline: #0000006b;
    border: 1px solid #0000006b;
}


.toggleDateForm:disabled {
    background-color: rgba(137, 160, 179, 0.5);
    color: #0000006b;
}

@media (max-width: 1200px) {
    .text {
        max-width: 250px;
    }

    .utils {
        margin-left: 0px;
    }

    .deleteBtn {
        left: 15px;
    }

    .btnReceived {
        right: 15px;
        left: auto;
    }
}

@media (max-width: 990px) {

    .text {
        max-width: 220px;
    }

    .deleteBtn {
        left: 10px;
    }

    .btnReceived {
        right: 10px;
        left: auto;
    }
}

@media (max-height: 990px) {

}

@media (max-width: 770px) {
    .text {
        max-width: 225px;
    }

    .message {
        font-size: 0.9rem;
    }

    .dialogsPage {
        margin-left: 85px;
    }

    .setMessageBtn {
        width: 25px;
        height: 25px;
        right: 5px;
        transition: opacity .1s linear;
    }

    .setMessageBtn.setMessageBtnReceived {
        left: 5x;
    }

    .DialogChat_messageSend__2VEOM {
        margin-right: 20px;
    }

    .setMessageBtnInner {
        width: 6px;
        height: 16px;
        top: -5px;
        left: 9px;
    }

}

@media (max-width: 575px) {
    .text {
        max-width: 220px;
    }

    .deleteBtn {
        left: 5px;
    }

    .btnReceived {
        right: 5px;
        left: auto;
    }

    .messageDate,
    .messageTime {
        margin-left: 0;
        bottom: 0;
        right: 0;
        font-size: 0.85rem;
        vertical-align: top;
    }

    .received:after {
        border-bottom: 10px solid #d9e9f5;
        border-left: 10px solid transparent;
        border-right: 10px solid transparent;
        bottom: -2px;
        left: -11px;
    }

    .send:after {
        border-bottom: 10px solid #f9f9f9;
        border-left: 10px solid transparent;
        border-right: 10px solid transparent;
        bottom: -2px;
        right: -11px;
    }

    .received:before {
        border-bottom: 11px solid #d9e9f5;
        border-left: 10px solid transparent;
        border-right: 10px solid transparent;
        bottom: -2px;
        left: -11px;
    }

    .send:before {
        border-bottom: 11px solid #f9f9f9;
        border-left: 10px solid transparent;
        border-right: 10px solid transparent;
        bottom: -2px;
        right: -11px;
    }

    .messageSend {
        margin-right: 15px;
    }
    .message {
        font-size: 1.0rem;
    }
}

@media (max-width: 480px) {
    .message {
        font-size: 1.1rem;
    }
    .text {
        max-width: 180px;
    }

    .dialogsPage {
        margin-left: 0px;
    }


}

@media (max-width: 375px) {
    .text {
        max-width: 180px;
    }

}

@media (max-width: 320px) {
    .text {
        max-width: 160px;
    }
    .message {
        font-size: 1.2rem;
    }
}