.PostData {
    margin-top: 60px;
    margin-bottom: 15px;
}

.Posts {
    margin-top: 50px;
    color: black;
    margin-bottom: 15px;
    max-width: 600px;
}


.PostForm {
    position: relative;
    border: 0px solid #e7e8ec;
}

.PostForm textarea {
    font-size: 1rem;
    height: 55px;
    width: 100%;
    background-color: #fff;
    border: 1px solid #e7e8ec;
    border-radius: 5px;
    padding: 15px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    transition: height .1s linear;

}

.PostForm textarea:focus {
    outline: #0000006b;
    border: 1px solid #0000006b;
    height: 90px;
}

.PostFormActive {
    width: 100%;
    height: 100px;
}

.PostForm:after {
    content: '';
    color: white;
    display: block;
    border: 1px solid #e7e8ec;
}

.PostForm button {
    position: absolute;
    bottom: -30px;
    right: 0;
    background-color: #fff;
    border-radius: 6px;
    border: 1px solid #e7e8ec;
    display: inline-block;
    cursor: pointer;
    color: black;
    font-size: 0.75rem;
    font-weight: bold;
    padding: 6px 12px;
    text-decoration: none;
    transition: background-color .1s linear,
    color .1s linear;
}

.PostForm button:hover {
    background-color: rgba(137, 160, 179, 0.5);
    color: #fff;
}

.PostForm button:focus {
    outline: #0000006b;
    border: 1px solid #0000006b;
}

.PostForm button:disabled {
    background-color: rgba(137, 160, 179, 0.5);
    color: #0000006b;
}

.Post {
    margin: 15px 0;
    background-color: #fff;
    border-radius: 5px;
    padding: 15px;
}

.postItem {
    display: flex;
    flex-direction: row;
}

.postInfo {
    margin-left: 20px;
    display: flex;
    flex-direction: column;
}

.photo img {
    border-radius: 50%;
    width: 60px;
}

.senderName {
    font-size: 1.5rem;
}

.addedAt {
    font-style: italic;
    color: #0000006b;
}

.postBody {
    margin-top: 10px;
    overflow-wrap: break-word;

}

.postBody:after {
    content: '';
    color: white;
    display: block;
    border: 1px solid #e7e8ec;
    cursor: pointer;
    margin: 20px 0 5px 0;
}

.postFooter {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.postFooter img {
    vertical-align: sub;
}


.likesCount img {
    width: 25px;
    transition: filter .1s linear;

}

.likesCount img:hover {
    cursor: pointer;
    filter: opacity(.5) drop-shadow(0 0 0 #1d5d90);
}

.views img {
    width: 25px;
}

@media (max-width: 770px) {
    .Posts {
        max-width: 770px;
    }
}
